import musicList from './music-info';

// Chaching needed Dom Elements
const audio = window.document.querySelector('.audio');
const musicPalyerSongTitle = window.document.querySelector(
  '.music-palyer__song-title'
);
const musicPalyerArtistName = window.document.querySelector(
  '.music-palyer__artist-name'
);
const musicPalyerImage = window.document.querySelector('.music-palyer__image');
const musicPalyerMusicBars = window.document.querySelector(
  '.music-palyer__music-bars'
);

const musicPalyerProgressTimeCurrent = window.document.querySelector(
  '.music-palyer__progress__time__current'
);

const musicPalyerProgressTimeDuration = window.document.querySelector(
  '.music-palyer__progress__time__duration'
);

const musicPalyerProgressBar = window.document.querySelector(
  '.music-palyer__progress__bar'
);

const musicPalyerControlsReplay = window.document.querySelector(
  '.music-palyer__controls__replay'
);
const musicPalyerControlsPrev = window.document.querySelector(
  '.music-palyer__controls__prev'
);
const musicPalyerControlsPlay = window.document.querySelector(
  '.music-palyer__controls__play'
);
const musicPalyerControlsNext = window.document.querySelector(
  '.music-palyer__controls__next'
);
const musicPalyerControlsMute = window.document.querySelector(
  '.music-palyer__controls__mute'
);

// local variable for musicPalyerIIFE
let isPlay = false;
let isMute = false;
let songCurrntIndex = 0;

// Helper Function
const playAudio = () => {
  audio.play();
  isPlay = true;
  // window.console.log('Play now');
  musicPalyerControlsPlay.classList.replace(
    'fa-play-circle',
    'fa-pause-circle'
  );
  musicPalyerControlsPlay.setAttribute('title', 'Pause');

  musicPalyerMusicBars.classList.remove('pause');
};

const pauseAudio = () => {
  audio.pause();
  isPlay = false;
  // window.console.log('Pause now');
  musicPalyerControlsPlay.classList.replace(
    'fa-pause-circle',
    'fa-play-circle'
  );

  musicPalyerControlsPlay.setAttribute('title', 'Play');
  musicPalyerMusicBars.classList.add('pause');
};

const muteAudio = () => {
  audio.volume = 0;
  isMute = true;
  musicPalyerControlsMute.classList.replace('fa-volume-up', 'fa-volume-mute');
};

const unmuteAudio = () => {
  audio.volume = 1;
  isMute = false;
  musicPalyerControlsMute.classList.replace('fa-volume-mute', 'fa-volume-up');
};

const displaySong = (song) => {
  const { author, title, songUrl, imgUrl } = song;
  audio.src = songUrl;
  musicPalyerImage.src = imgUrl;
  musicPalyerSongTitle.textContent = title;
  musicPalyerArtistName.textContent = author;
};

const prevSong = () => {
  songCurrntIndex -= 1;
  if (songCurrntIndex < 0) {
    songCurrntIndex = musicList.length - 1;
  }
  displaySong(musicList[songCurrntIndex]);
};

const nextSong = () => {
  songCurrntIndex += 1;
  if (songCurrntIndex >= musicList.length) {
    songCurrntIndex = 0;
  }
  displaySong(musicList[songCurrntIndex]);
};

const timeCalc = (timeWithMins) => {
  let mins = Math.floor(timeWithMins / 60);
  mins = mins < 10 ? `0${mins}` : mins;

  let secs = Math.floor(timeWithMins % 60);
  secs = secs < 10 ? `0${secs}` : secs;

  return `${mins}:${secs}`;
};

const progressCalc = (currntTimeWithMins, durationWithMins) =>
  (currntTimeWithMins / durationWithMins) * 100;

// Add click event to stopwatchStartBtn to handle  stopwatch start task.
musicPalyerControlsPlay.addEventListener('click', () => {
  // eslint-disable-next-line no-unused-expressions
  isPlay ? pauseAudio() : playAudio();
});

musicPalyerControlsMute.addEventListener('click', () => {
  // eslint-disable-next-line no-unused-expressions
  isMute ? unmuteAudio() : muteAudio();
});

musicPalyerControlsPrev.addEventListener('click', () => {
  prevSong();
  playAudio();
});

musicPalyerControlsNext.addEventListener('click', () => {
  nextSong();
  playAudio();
});

const songFinished = () => {
  audio.currentTime = 0;
  window.console.log('finish');
  musicPalyerProgressBar.setAttribute('value', '0');
  musicPalyerProgressTimeCurrent.textContent = '00:00';

  musicPalyerMusicBars.classList.add('pause');

  musicPalyerControlsPlay.classList.replace(
    'fa-pause-circle',
    'fa-play-circle'
  );
  isPlay = false;
};

audio.addEventListener('timeupdate', () => {
  if (isPlay) {
    const { currentTime } = audio;
    const duration = audio.duration || 0;

    musicPalyerProgressTimeCurrent.textContent = timeCalc(currentTime);
    musicPalyerProgressTimeDuration.textContent = timeCalc(duration);

    musicPalyerProgressBar.setAttribute(
      'value',
      `${progressCalc(currentTime, duration)}`
    );
  }
});

musicPalyerProgressBar.addEventListener('click', (e) => {
  window.console.log(e);

  const songDuration = audio.duration;
  const progressBarWidth = e.target.clientWidth;
  const clickOffsetX = e.offsetX;

  window.console.log(progressBarWidth, ' ', clickOffsetX);
  window.console.log(progressBarWidth / clickOffsetX);

  const clickOffsetTime = (clickOffsetX / progressBarWidth) * songDuration;

  audio.currentTime = clickOffsetTime;
});

musicPalyerControlsReplay.addEventListener('click', () => {
  pauseAudio();
  songFinished();
  playAudio();
  window.console.log('replay');
});

audio.addEventListener('ended', songFinished);

export const musicPlayerInit = () => displaySong(musicList[songCurrntIndex]);

